*
{
    box-sizing: border-box;
}

body
{
    margin: 0;
    overflow-x: hidden;
    background-color: #292929;
    background-repeat: repeat;
    -webkit-font-smoothing: antialiased;
    background-image: url(data:image/png;base64,);
    font-family: 'Roboto Condensed', sans-serif;
}

button.btn
{
    color: #fff;
    cursor: pointer; 
    border-radius: 4px;
    border-style: solid;
}

button.btn.btn-red
{
    background-color: #f44336;    
}

button.btn.btn-green
{
    background-color: #4caf50;    
}

button.btn.btn-purple
{
    background-color: #673ab7;
}

button.btn:disabled
{
    opacity: .75;
}

.container
{
    margin: 0 auto;
    max-width: 1200px;
}

.box-shadow
{
    box-shadow: 0 4px 10px 0 rgba(0,0,0,0.19), 0 4px 20px 0 rgba(0,0,0,0.19);
}

.body
{
    left: 0;
    right: 0;
    top: 186px;
    z-index: 1;
    margin: auto;
    bottom: 146px;
    position: fixed;
    max-width: 1200px;
}

.shadow
{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;    
    z-index: 10;
    display: none;
    position: fixed;
    transition: all .3s ease;
    background-color: rgba(0,0,0,.75);
}

.shadow.opened
{
    opacity: 1;
    display: block;
}

.shadow.shadow-print
{
    z-index: 15;
}

.overflow-y
{
    overflow-y: auto;
}
.shadow .modal
{    
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    position: absolute;
    background-color: #444;
}

.shadow .modal .header
{
    height: 60px;
    color: #fff;
    padding: 20px;    
    text-transform: uppercase;    
}

.shadow .modal .header button
{
    top: 0;
    right: 0;
    width: 61px;
    height: 61px;
    float: right;
    border: none;
    color: #fff;
    padding: 18px;
    cursor: pointer;
    font-size: 24px;
    position: absolute; 
    background-color: transparent;    
}

.shadow .modal .body
{
    left: 0;
    right: 0;
    top: 60px;
    bottom: 60px;
    padding: 20px;
    color: #fff;
    overflow-y: auto;
    position: absolute;    
}

.shadow .modal .footer
{
    left: 0;
    right: 0;
    bottom: 0;
    height: 60px;
    padding: 20px;
    position: absolute;
    border-top: 1px solid #666;
}

.shadow .modal .footer button:disabled
{
    opacity: .75;
}
.shadow-print .success
{
    margin: 20px;
    padding: 20px;
    color: #fff;
    font-size: 24px;
    text-align: center;
    background-color: #4caf50;
}

.button-print,
.button-print-close
{
    right: 40px;
    width: 56px;
    height: 56px;
    color: #fff;
    padding: 14px;
    position: fixed;
    font-size: 24px;
    border-radius: 50% !important;
}

.button-print:focus,
.button-print-close:focus
{
    padding: 12px;
    border: 2px solid #fff;
}

.button-print
{
    bottom: 94px;
    background-color: #ff9800;
}

.button-print-close
{
    bottom: 20px;
    background-color: #f44336;
}

@media print
{
    .bar,
    .body,
    button,
    #header,
    #footer,
    .success,
    .shadow-modal
    {
        display: none !important;
    }
    .shadow
    {
        position: relative;
        overflow-y: hidden;
        background-color: transparent;
    }
    body
    {
        background-image: none;
        background-color: transparent;
    }
}

